<template>
	

<div class="personalAuthen_page">
	
	<header-top title='' back></header-top>
	<div class="fixed-top-div"></div>

    <div class="content gerenrenzheng">

        <form action="" class="layui-form">

            <div class="tips1">
                
                
                
            </div>


            <div class="form-tit">
                
            </div>
            <div class="form-group">
                <label for="name"></label>
                <input type="text" v-model:value="user.company" id="name" placeholder="">
            </div>
            <div class="form-group">
                <label for="principal"> </label>
                <input type="text" v-model:value="user.principal"  id="principal"  placeholder="">
            </div>
            <div class="form-group">
                <label for="idCard"></label>
                <input type="text" v-model:value="user.principal_id"  id="idcard" placeholder="">
            </div>
            <div class="form-group">
                <label for="phone"></label>
                <input type="text" v-model:value="user.phone"  id="phone" placeholder="">
            </div>
            <div class="form-group">
                <label for="area">/</label>
                <input type="text" readonly @click="showSelectCityBox = true " v-bind:value='user.city' id="area" placeholder="">
            </div>
            <div class="form-group">
                <label for="address"></label>
                <input type="text" v-model:value="user.address" id="address" placeholder="">
            </div>

            <div class="form-group from-img">
                <div>
                    <p></p>
                    <p class="font-style-red"></p>

                    <!--  -->
                    <drop-zone id="myVueDropzone" ref='uploadImg' :useFontAwesome="false" enctype="multipart/form-data" methods='post' 
                      :autoProcessQueue=false

                      :maxFileSizeInMB=5

                      :maxNumberOfFiles=1
                      :language="{dictRemoveFile:'',dictCancelUpload:'',dictFileTooBig:'{{filesize}}Mib{{maxFilesize}}Mib'}"
                      :headers="{cache:false,processData:false,contentType:false}"
                      acceptedFileTypes="image/*" 
                      url="http://192.168.0.123:8080/beta6.3/loadfile" 
                      v-bind:preview-template="template"
                      v-on:vdropzone-success="showSuccess"
                      v-on:vdropzone-removed-file="removeImg"
                      v-on:vdropzone-complete="complete"
                    >



                    <div @click="deleteImg(index)" class="dz-preview dz-image-preview" style="width:100%;height:100%;padding:.1rem;margin:0;" v-for=" (item,index) in imgList" v-if="item">
                        <div class="dz-image" style="width:100%;height:100%;display:flex;justify-content:center;align-items:center;">
                            <img style="height:8rem;width:100%" :src="item"/>
                        </div>
                        <div class="dz-progress">
                          <span class="dz-upload" data-dz-uploadprogress=""></span>
                        </div>
                        <a class="dz-remove" data-dz-remove="" style="background:#3883FF;opacity:1;"></a>
                    </div>





                    <div class="dz-default dz-message" v-show=" imgList.length <= 0 "><span><i class="material-icons"></i><br></span></div>

                        <!-- Optional parameters if any! -->
                        <!-- <input type="hidden" name="token" value="xxx"> -->
                    </drop-zone>




                </div>
            </div>

            <div class="tips2">
                
                .jpg.jpeg
                png.gif10MB
            </div>

            <div class="form-btn">
                <!-- <p v-if="personReal" class="btn btn-button center" v-text="statusText"></p> -->
<!--                 <input type="submit" @click.prevent="uploadPhoto" value='' class="btn btn-button">
                <input type="submit" @click.prevent="uploadPhoto" value='' class="btn btn-button">
                <input type="submit" @click.prevent="uploadPhoto" value='' class="btn btn-button">

                <p class="btn btn-button"></p>
                //this.statusText = {0:"",1:"",2:""}[obj.state];
 -->

                <input v-if='user.state==0' type="submit" @click.prevent="uploadPhoto" value="  " class="btn btn-button">
                <p v-else-if='user.state==1' class='btn btn-button'></p>
                <p v-else-if='user.state==2' class='btn btn-button'></p>
                <input v-else type="submit" @click.prevent="uploadPhoto" value="  " class="btn btn-button">

            </div>

        </form>



    </div>


    <!--  -->
    <transition>
      <city v-show='showSelectCityBox' @selectCity='selectCity'></city>
    </transition>



</div>




</template>

<script>

// import headerTop from '@/components/header/header'	
import headerTop from '@/components/header/header'

import dropZone from 'vue2-dropzone'
import city from '@/components/common/city'

console.log(dropZone)

export default{

	components:{
		headerTop,
    dropZone,
    city
	},
  data(){
    return{
      dropZoneBox:null,

      showSelectCityBox:false,

      user:{
          vip:'',
          company:'',
          principal:'',
          fdb:'',
          principal_id:'',
          phone:'',
          license:'',
          aId1:'',
          aId2:'',
          aId3:'',
          address:'',
          registrid:'123456',
          city:'',
          // state:'',
      },

      imgList:[],//
      newImgList:[],//
    }
  },
  created(){
    layer.closeAll();
  },
  mounted(){
    this.$nextTick(()=>{

      this.user.vip = this.$store.getters.getVip;

      //
      this.queryInfo();

    })
  },

  methods:{

    checkInput(){


        if(this.user.company == ""){
          layer.msg("")
          return;
        }
        if(this.user.principal == ""){
          layer.msg("")
          return;
        }
        if(this.user.phone == ""){
          layer.msg("")
          return;
        }
        if(this.user.city == ""){
          layer.msg("/")
          return;
        }
        if(this.user.address == ""){
          layer.msg("")
          return;
        }
        //imgListuploadimg
        if(this.imgList.length < 1 && this.imgList.length < 1){
          layer.msg("")
          return;
        }

        return true;


    },
    //
    deleteImg(index){
      
      this.imgList.splice(index,1);

    },
    //
    complete(){

    },

    //
    queryInfo(){




      this.$http.post('/queryqysmrz?vip=',{vip:this.$store.getters.getVip}).then((res)=>{
          console.log(res.data);

          //
          if(res.data.code==0 && res.data.response.state ){
              var obj = res.data.response;

              this.user.company = obj.company;
              this.user.principal = obj.principal;
              this.user.aId1 = obj.a_id1;
              this.user.aId2 = obj.a_id2;
              this.user.aId3 = obj.a_id3;

              var img = obj.license;
              this.imgList = obj.license.split(',');
              this.user.license = img.substring(img.lastIndexOf('/')+1,img.length);

              this.user.phone = obj.phone;
              this.user.principal_id = obj.principal_id;
              this.user.address = obj.address;
              this.user.state = obj.state;
              this.user.city = obj.a_id3;
              this.images = obj.license;


          }

      }).catch(function(res){
          console.log(res);
      });







    },
    //
    submitForm(){
      let url;

      //
      if(this.user.state == 0){
        url = "updateSmrzqy"
      }else{
        url = "smrzqy"
      }


      console.log(this.user);

      // return;




      this.$http.post(url,this.user).then((res)=>{
          if(res.data.code==0){
            
            layer.msg("1!",{time:1.2},()=>{
              location.reload();
            });

          }else{
              layer.msg(res.data.message);
          }

      }).catch(function(res){
          console.log(res);
      });

    },
    //
    uploadPhoto(){

      //
      if(!this.checkInput()) return;

      //
      if(this.$refs.uploadImg.dropzone.files.length){
        this.$refs.uploadImg.processQueue();
      }else{
        this.submitForm();
      }

    },
    showSuccess(file,res){
      //
      if(res.code==0){
        this.user.license = res.response;
        //
        this.submitForm();
      }

    },
    //
    removeImg(file){

      this.user.license = "";

    },
    //
    selectCity(item){
      
      //
      if(item.getAttribute("data-level")==3){
        this.user.aId1 = item.getAttribute("data-topid");
        this.user.aId2 = item.getAttribute("data-superid");
        this.user.aId3 = item.getAttribute("data-id");
        //
      }else if(item.getAttribute("data-level")==2 && item.getAttribute("data-id") != "QY201606011002" ){
        this.user.aId1 = item.getAttribute("data-superid")
        this.user.aId2 = item.getAttribute("data-id")
        this.user.aId3 = ""
      }else{
        layer.open({
          type:"skin",
          content:"/",
          // shade:"background:rgba(0,0,0,.4);",
          time:1.2,
        })
        return;
      }

      this.user.city = item.textContent;
      console.log(item);
      this.showSelectCityBox = false;
    },
    template() {
          return `
                  <div class="dz-preview dz-file-preview" style="width:100%;height:100%;padding:.1rem;margin:0;">
                      <div class="dz-image" style="width:100%;height:100%;display:flex;justify-content:center;">
                          <img data-dz-thumbnail />
                      </div>
                      <div class="dz-progress">
                        <span class="dz-upload" data-dz-uploadprogress></span>
                      </div>

                      <div class="dz-error-message" style="top:inherit;"><span data-dz-errormessage></span></div>

                      <div class="dz-details">
                        <div class="dz-size"><span data-dz-size></span></div>
                        <div class="dz-filename"><span data-dz-name></span></div>
                      </div>
                  </div>
              `;
      }



  }



}


</script>
<style lang='less' scoped>



@import '../../../assets/css/variable.less';



.personalAuthen_page{

	.fixed();

}


#myVueDropzone{
  padding:0;


  .dz-image{
    width:100%;
    height:100%;
    display:flex;
    justify-content: center;
  }

  .dz-remove{
    left:0;
    right:0;
    width:3rem;
    font-size:.6rem;
    margin:0 auto;
  }
}

/*//*/

.gerenrenzheng{

  background: #fff;
  .tips1,.tips2{
    background: #EAEAEA;
    color:#f00;
    padding:8/@root-font-size 9/@root-font-size;
    font-size:.6rem;
    line-height:.9rem;
  }

/*  .tips2{
    font-size:.6rem;
    color:#f00;
    padding:8/@root-font-size 9/@root-font-size;
  }*/


  
  .border-bottom{
    border-bottom:1px solid #E4E4E4;
  }

  .form-tit{
    text-align: center;
    line-height:35/@root-font-size;
    .border-bottom;
    font-size:.78rem;
  }

  .form-group{
    .border-bottom;
    padding:10/@root-font-size 12/@root-font-size;

    display: flex;
    justify-content: flex-start;

    label{

      width:35%;
      font-size:.7rem;
      display: flex;
      align-items: center;

      

    }
    input{
      width:72%;
      font-size:.7rem;
      /*box-sizing:border-box;*/
    }

    

  }

  .from-img{

/*  	display: flex;
  	justify-content:space-around; */

    div{
/*		width:49%;*/
		flex:1;

    }
    img{
      height:90/@root-font-size;
      width:100%;
    }
    p{
      font-size:.7rem;
      margin-bottom:10/@root-font-size;
    }
    p.img{
      position: relative;
      color:#f00;
    }
    input{
      opacity:0;
      width:100%;
      height:100%;
      left:0;
      top:0;
      position: absolute;

    }
  }

  .form-btn{
    padding:8/@root-font-size 9/@root-font-size;

    .btn{
    	font-size:.8rem;
      width:100%;
      line-height:38/@root-font-size;
      background: #389E3A;
      color:#fff;
      border-radius:4px;
      text-align: center;
    }
  }


}


</style>


